.page-header {
  display: flex;
  flex-direction: column;
  padding-top: 11px;
  background-color: @color-white;
  color: @color-black;

  @media (min-width: @tablet-width) {
    padding-top: 24px;
  }

  @media (min-width: @desktop-width) {
    padding-top: 53px;
    background-color: transparent;
  }

  &__wrapper {
    @media (min-width: @tablet-width) {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
  }
}

.main-header {
  .page-header__wrapper {
    position: relative;

    &::after {
      position: absolute;
      top: -53px;
      left: 50%;
      width: 720px;
      height: 696px;
      content: "";
      background-color: @color-basic-green;
      background-image: url(".././img/index-background-desktop@1x.jpg");
      background-repeat: no-repeat;
      background-position: top center;
      background-size: cover;
      z-index: -1;

      @media (min-resolution: 2dppx) {
        background-image: url(".././img/index-background-desktop@2x.jpg");
      }
    }
  }
}

.logo {

  // .logo__link

  &__link {
    display: block;
    text-decoration: none;

    &:hover {
      opacity: 0.8;
    }

    &:active {
      opacity: 0.6;
    }

    @media (min-width: @tablet-width) {
      .header-logo {
        display: block;
      }

      .header-logo--mobile {
        display: none;
      }
    }
  }

  // .logo__link--header

  &__link--header {
    position: relative;
    left: -5px;
    padding-bottom: 13px;

    @media (min-width: @tablet-width) {
      left: -10px;
      padding-bottom: 0;
    }
  }

  // .logo__img--header

  &__img--header,
  &__img--footer {
    max-width: 100%;
    height: auto;
  }

  &__img--header {
    @media (min-width: @tablet-width) {
      height: 51.8px;
    }

    @media (min-width: @desktop-width) {
      position: relative;
      left: 16px;
      height: 60px;
      width: 202px;
    }
  }

  // .logo__link--footer

  &__link--footer {
    width: 128px;
    height: 24px;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 33px;

    &:hover {
      opacity: 0.8;
    }

    &:active {
      opacity: 0.6;
    }

    @media (min-width: @tablet-width) {
      margin: 0;
      margin-right: 18px;
    }

    @media (min-width: @desktop-width) {
      padding-top: 8px;
    }
  }
}

.header-logo {
  display: none;
}
