.program-item {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-template-rows: auto 1fr auto;
  grid-template-areas: "img title title title" "text text text text" " link link link link";
  grid-row-gap: 23px;
  grid-column-gap: 20px;
  align-items: center;
  background-color: #f2f2f2;
  padding-top: 24px;
  padding-left: 20px;
  padding-right: 20px;
  margin-bottom: 20px;
}
