}
@media (min-width: 768px) {
  .program-item {
    grid-template-columns: 1fr 1fr 244px;
    grid-template-areas: "title title img" "text text img" " link link img";
    grid-row-gap: 24px;
    grid-column-gap: 17px;
    padding-left: 52px;
    padding-top: 79px;
    padding-bottom: 40px;
    margin-bottom: 27px;
  }
  .program-item:nth-of-type(2) .program-item__description {
    max-width: 342px;
