    max-width: none;
  }
  .program-item__img-wrapper {
    grid-area: img;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50px;
    height: 50px;
    margin-left: 2px;
    border-radius: 50%;
    background-color: #68b738;
  }
  .program-item__img {
    position: absolute;
    top: 45%;
    left: 50%;
    transform: translate(-50%, -50%);
