  .program-item__description {
    margin: 0;
    grid-area: text;
    line-height: 1.28571429;
  }
  .program-item__footer {
    grid-area: link;
    width: 100%;
    padding-top: 18px;
    padding-bottom: 23px;
    border-top: 1px solid #d9d9d9;
