.modal {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 5;
  width: 99vw;
  height: 100vh;
  background-color: rgba(255, 255, 255, 0.9);
  backdrop-filter: blur(5px);

  // .modal__toggle

  &__toggle {
    position: absolute;
    top: 10px;
    right: 13px;
    padding: 0;
    width: 30px;
    height: 30px;
    outline: none;
    border: none;
    cursor: pointer;
    z-index: 2;
    background: transparent;

    &::before,
    &::after {
      content: "";
      position: absolute;
      top: 12px;
      right: 1px;
      width: 24px;
      height: 2px;
      background-color: @color-black;
    }

    &::before {
      transform: rotate(45deg);
      box-shadow: none;
    }

    &::after {
      transform: rotate(-45deg);
    }
  }
}

.form-order {
  padding: 0;
  padding-left: 13px;
  padding-right: 13px;
  padding-bottom: 24px;
  margin: 0 auto;
  margin-top: 50vh;
  transform: translateY(-50%);

  border: none;
  border-top: 1px solid @color-basic-gray;
  border-bottom: 1px solid @color-basic-gray;
  outline: none;
  background-color: lighten(@color-basic-green, 50%);

  .fieldset__title {
    left: 0;
    font-size: @font-size*1.8;
    line-height: 1.5;
    color: @color-special_dark-grey;
  }

  @media (min-width: @tablet-width) {
    padding-left: 40px;
    padding-right: 40px;
    padding-bottom: 35px;

    .form__label {
      margin-bottom: 35px;
    }

    .fieldset__title {
      font-size: @font-size*3.6;
    }
  }

  @media (min-width: @desktop-width) {
    padding-left: 60px;
    padding-right: 60px;
    padding-bottom: 35px;
  }
}

.order-fieldset {
  padding: 0;
  margin: 0;
  border: none;
  outline: none;
}
